.social_dragging {
  list-style: none; }

.wp-block-qtd-blocks-team-members {
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px; }
  .wp-block-qtd-blocks-team-members.has-1-columns .wp-block-qtd-blocks-team-member {
    width: calc(100%/1 - 20px); }
  .wp-block-qtd-blocks-team-members.has-2-columns .wp-block-qtd-blocks-team-member {
    width: calc(100%/2 - 20px); }
  .wp-block-qtd-blocks-team-members.has-3-columns .wp-block-qtd-blocks-team-member {
    width: calc(100%/3 - 20px); }
  .wp-block-qtd-blocks-team-members.has-4-columns .wp-block-qtd-blocks-team-member {
    width: calc(100%/4 - 20px); }
  .wp-block-qtd-blocks-team-members.has-5-columns .wp-block-qtd-blocks-team-member {
    width: calc(100%/5 - 20px); }
  .wp-block-qtd-blocks-team-members.has-6-columns .wp-block-qtd-blocks-team-member {
    width: calc(100%/6 - 20px); }
  .wp-block-qtd-blocks-team-members .wp-block-qtd-blocks-team-member {
    background-color: #fffcfc;
    color: #322e2e;
    padding: 2px;
    border: 1px solid #0d0f15;
    margin: 10px; }
    .wp-block-qtd-blocks-team-members .wp-block-qtd-blocks-team-member__title {
      color: #d24848 !important;
      font-size: 3rem;
      font-weight: lighter;
      font-style: oblique;
      text-transform: capitalize;
      margin-bottom: 0;
      padding: 10px; }
    .wp-block-qtd-blocks-team-members .wp-block-qtd-blocks-team-member__info {
      color: #641414 !important;
      font-size: 2rem;
      font-weight: bolder;
      margin-bottom: 5px;
      padding: 10px; }
    .wp-block-qtd-blocks-team-members .wp-block-qtd-blocks-team-member__addIcon {
      padding: 2px;
      line-height: 1;
      border: 1px dashed #00d082;
      cursor: pointer; }
    .wp-block-qtd-blocks-team-members .wp-block-qtd-blocks-team-member__linkForm {
      border: 1px solid #eaeaea;
      padding: 10px;
      margin-top: 10px; }
    .wp-block-qtd-blocks-team-members .wp-block-qtd-blocks-team-member__removeLink {
      color: #d23535;
      font-size: 14px;
      text-decoration: underline;
      cursor: pointer; }
    .wp-block-qtd-blocks-team-members .wp-block-qtd-blocks-team-member__social ul {
      list-style: none;
      line-height: 1;
      margin-left: -5px;
      margin-right: -5px; }
      .wp-block-qtd-blocks-team-members .wp-block-qtd-blocks-team-member__social ul li {
        display: inline-block;
        line-height: 1;
        margin: 5px; }
        .wp-block-qtd-blocks-team-members .wp-block-qtd-blocks-team-member__social ul li a {
          border-bottom: none; }
        .wp-block-qtd-blocks-team-members .wp-block-qtd-blocks-team-member__social ul li.is-selected {
          outline: 2px solid #99797e; }
        .wp-block-qtd-blocks-team-members .wp-block-qtd-blocks-team-member__social ul li svg {
          vertical-align: middle;
          color: #4e4e4e; }
